import { opportunityPublicCustomerDetail, visitList } from '../../api/api.js'

export default {
	data() {
		return {
			id: null,
			province: null,
			city: null,
			district: null,
			name: null,
			intro: null,
			updateTime: null,
			recordList: []
		};
	},
	onLoad(option) {
		this.id = option.id
		// 公海客户详情
		opportunityPublicCustomerDetail({id: this.id}).then(res => {
			console.log('customerDetailRes:', res)
			let _d = res.data
			this.province = _d.province
			this.city = _d.city
			this.district = _d.district
			this.name = _d.name
			this.intro = _d.intro
			this.updateTime = _d.updateTime
		})
		// 拜访记录分页列表
		visitList({id: this.id}).then(res => {
			console.log('visitRes:', res)
			this.recordList = res.data.list
		})
	}
}
